import React, { Component } from "react";
import "./events.css";

import EventCard from "../components/eventCard";
import legacyBricks from "../images/legacyBricks.jpg";
import bookSale1 from "../images/bookSale1.jpg";
import bookSale2 from "../images/bookSale2.jpg";
import bookSale3 from "../images/bookSale3.jpg";
import legacyBrickForm from "../files/legacy_brick_form.docx";
import downloadIcon from "../images/downloadIcon.png"
import { BrowserRouter as Router, Link } from "react-router-dom";
 const legacyBrickInfo="Until July 1st, for just $75 you can buy a two line, custom engraved Legacy Brick. Legcay Bricks will be used in the landscaping around the building. Click below to download the form and follow the instructions to purchase."
const bookSaleInfo= "There's another Book Sale coming soon!  3,000+ books to peruse. Old and New!  Something for everyone, every age, action packed to cook book. Date and time yet to be announced.";
const eventCard1 = (
  <EventCard
    eventImage={legacyBricks}
    title={"Legacy Brick Fundraiser"}
    information={legacyBrickInfo}
    startDate={"Now"}
    endDate={"While Supplies Last"}
    width={"25%"}
    style={{ minWidth: "300px" }}
    height={"auto"}
    endTime={"7"}
    startTime={"24"}
    content={
      <div>
        <Router>
          <div     >
            <Link to={legacyBrickForm} target="_blank" className="downloadLink"  >
            <img className="downloadIcon" src={downloadIcon}></img> Legacy_Brick_Form.pdf
            </Link>
          </div>
        </Router>
      </div>
    }

  ></EventCard>
);

const eventCard2 = (
  <EventCard
    eventImage={bookSale1}
    title={"Spring Book Sale"}
    information={
      bookSaleInfo
    }
    date={"tbd"}
    width={"25%"}
    height={"auto"}
    startDate={"TBA"}
    endDate={"TBA"}
    startTime={"TBA"}
    endTime={"TBA"}
  ></EventCard>
);
const eventCard3 = (
  <EventCard
    eventImage={bookSale2}
    title={"Summer Book Sale"}
    information={
      bookSaleInfo
    }
    date={"tbd"}
    width={"25%"}
    height={"auto"}
    startDate={"TBA"}
    endDate={"TBA"}
    startTime={"TBA"}
    endTime={"TBA"}
  ></EventCard>
);
const eventCard4 = (
  <EventCard
    eventImage={bookSale3}
    title={"Fall Book Sale"}
    information={
      bookSaleInfo
    }
    date={"tbd"}
    width={"25%"}
    height={"auto"}
    startDate={"TBA"}
    endDate={"TBA"}
    startTime={"TBA"}
    endTime={"TBA"}
  ></EventCard>
);

const eventCard1Mobile = (
  <EventCard
    eventImage={legacyBricks}
    title={"Legacy Brick Fundraiser"}
    information={legacyBrickInfo}
    startDate={"Now"}
    endDate={"While Supplies Last"}
    width={"85%"}
    height={"auto"}
    endTime={"7"}
    startTime={"24"}
    content={
      <div>
        <Router>
          <div>
            <Link
              to={legacyBrickForm}
              target="_blank"
              className="downloadLink"
            >
                <img className="downloadIcon" src={downloadIcon}></img> Legacy_Brick_Form.pdf
            </Link>
          </div>
        </Router>
      </div>
    }
    
  ></EventCard>
);

class events extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidUpdate(prevProps, prevState) {}
  componentDidMount() {}
  componentWillUnmount() {}

  render() {
    return (
      <div className="eventsParent">
        <div className="eventsContainer">
          <h1 className="eventsHeading">Events / Fundraisers</h1>
          {this.props.mobile && (
            <div className="eventCardContainerMobile">{eventCard1Mobile} </div>
          )}
          {!this.props.mobile && (
            <div className="eventCardContainer">
              {eventCard1} {eventCard2} {eventCard3} {eventCard4}
            </div>
          )}
        </div>
      </div>
    );
  }
}

export default events;
