import React, { Component } from 'react';
import Profile from "../components/profile"
import Bill from '../images/Bill.png';
import Maralyn from '../images/Maralyn.JPG';
import Jim from '../images/Jim.jpg';
import "./membersMobile.css";
class member3 extends Component {
    render() {
        return (
            <div className="parentContainerMembersMobile" style={{height: (this.props.height-50)+'px' , postion:"relative", bottom:'0px'}}>
                 <h1 className="pageTitleMobile">Board Members</h1>
              <Profile
          Name={"Bill Cox"}
          Description={"Board Member"}
          Image={Bill}
          width={"50%"}
          height={"25%"}
        />

        <Profile
          Name={"Maralyn Killorn"}
          Description={"Board Member"}
          Image={Maralyn}
          width={"50%"}
          height={"25%"}
        />
        <Profile
        Name={"Jim Honeyford"}
          Description={"Honorary Board Member"}
          Image={Jim}
          width={"50%"}
          height={"25%"}>
        </Profile>

     </div>
        );
    }
}

export default member3;