import React, { Component } from "react";
import "./sponsors.css";
import  appleKing from "../images/apple_king.jpg";
import yakimaFruit from "../images/yakima_fruit.jpg";
import heritageBank from "../images/heritage.jpg";
import fredMeyer from "../images/fredMeyer.JPG";
import walmart from "../images/walmart.png";
class sponsors extends Component {
  constructor(props) {
    super(props);
  }



  render() {
    return (
      <div>
        {this.props.Mobile && <div className="sponsorsParent">
        <h2 className="sponsorsHeading"> A Special Thanks To Our Sponsors.</h2>
                <div className="sponsorsContainer">
                    <div className="logoContainer"> 
                    <img className= "logo" src={appleKing} alt=""></img>
                    <img className= "logo" src={yakimaFruit} alt=""></img>
                    <img className= "logo" src={heritageBank} alt=""></img>
                    <img className= "logo" src={fredMeyer} alt=""></img>      <img className= "logo" src={walmart} alt=""></img>
                    </div>
                </div>
               

            </div>}
        {!this.props.Mobile && < div  className="sponsorsParent">
        <h2 className="sponsorsHeading"> A Special Thanks To Our Sponsors.</h2>
                <div className="sponsorsContainer">
                    <div className="logoContainer"> 
                    <img className= "logo" src={appleKing} alt=""></img>
                    <img className= "logo" src={yakimaFruit} alt=""></img>
                    <img className= "logo" src={heritageBank} alt=""></img>
                    <img className= "logo" src={fredMeyer} alt=""></img>      <img className= "logo" src={walmart} alt=""></img>

                    </div>
                </div>
            
            </div>}
      </div>
    );
  }
}

export default sponsors;
