import React from "react";
import "./membersMobile.css";
import Profile from "../components/profile";

import Kathy from "../images/Kathy.jpg"
import Helen from "../images/helen.jpg";
import Theresa from "../images/Theresa.JPG";

class members extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
        <div className="parentContainerMembersMobile" style={{height: (this.props.height-50)+'px' , postion:"relative", bottom:'0px'}}>
             <h1 className="pageTitleMobile">Board Members</h1>
            <Profile
          Name={"Kathy Cluck"}
          Description={"Secretary"}
          Image={Kathy}
          width={"50%"}
          height={"30%"}
        />
            <Profile
          Name={"Helen Canatsey"}
          Description={"Board Member"}
          Image={Helen}
          width={"50%"}
          height={"30%"}
        />
        <Profile
          Name={"Theresa Charvet"}
          Image={Theresa}
          Description={"Board Member"}
          width={"50%"}
          height={"30%"}
        />
   
   
     
      </div>
    );
  }
}
export default members;
