import React, { Component } from 'react';
import EventCard from "../components/eventCard"
import bookSale3 from "../images/bookSale3.jpg";
const bookSaleInfo= "There's another Book Sale coming soon!  3,000+ books to peruse. Old and New!  Something for everyone, every age, action packed to cook book. Date and time yet to be announced.";

const eventCard4Mobile = (
    <EventCard
      eventImage={bookSale3}
      title={"Fall Book Sale"}
      information={
        bookSaleInfo
      }
      date={"tbd"}
      width={"85%"}
        height={"auto"}
        startDate={"TBA"}
    endDate={"TBA"}
    startTime={"TBA"}
    endTime={"TBA"}
    ></EventCard>
  );
class eventsMobile4 extends Component {
    constructor(props) {
        super(props);

    }

    

    render() {
        return (
            <div>
            <div className="eventsParent" >
            <div className="eventsContainer">
        <h1 className="eventsHeading">Events / Fundraisers</h1>
       <div className="eventCardContainerMobile">{eventCard4Mobile} </div>
   </div>
 </div>
       </div>
        );
    }
}



export default eventsMobile4;